.gallery {
    display: flex;
    width: 80%;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: space-between;
    padding-left: 0;
    list-style: none;

}

img {
    width: 240px;
    height: 160px;
}

.stats {
    display: flex;
    justify-content: space-between;
    font-size: 10px;
    margin-bottom: 15px;
}

.material-icons{
    font-size: 18px;
    display: flex;
    justify-content: center;

}